import { useEffect } from "react";
import Card from "./Elements/Card";
import './scss/ListTheme.scss'
const ListTheme = ({ postData, completeDataObject, CardStyle, ThemeStyle,  clickToShowPopUp, loadBranding }) => {

  useEffect(() => {
     loadBranding()
  }, []);

  return <div className="tb_l_post_container">
    {
      ((postData && postData.length > 0)) && postData.map((item, index) => {
        const cardData = completeDataObject[item];
        return <Card Post={cardData} CardStyle={CardStyle} ThemeStyle={ThemeStyle}  key={`wdt_crd_${index}_${item.id}`} itemIndex={index} clickToShowPopUp={clickToShowPopUp} />
      })
    }
  </div>
}
export default ListTheme
