import React from "react";
import AuthorDetails from "./AuthorDetails";
import PostContent from '../../Elements/PostContent/PostContent'
import { PostTrimContent } from "../../../../utils";
import { isTagShop } from "../../../../actions/api";
import { NO_PRODUCT } from "../../../../constants";
import Media from "./Media";
import InViewWrapper from "../../Elements/InViewWrapper";
const trim_content_length = 200;
const Card = ({ Post, clickToShowPopUp, itemIndex, CardStyle, ThemeStyle }) => {

  const mediaType = Post.type !== 1 ? true : false;
  const postTextContent = CardStyle.content.trimcontent ? PostTrimContent(Post.content.text, trim_content_length) : Post.content.text
  const products = Post.products
  return <InViewWrapper>
        {({ ref, inView }) => (<div id={`tb_l_id-${Post.id}`} className={`tb_l_post_wrapper`} tb-network={Post.networkId} role="article" aria-label={`Post ${parseInt(itemIndex) + 1},${postTextContent}`} style={{padding: CardStyle.style.padding / 2, opacity: inView ? 1 : 0, transition: inView ? 'opacity .5s ease-in-out' : '' }} tabIndex="0" ref={ref} >
            <div className="tb_l_post_in" onClick={clickToShowPopUp(itemIndex, Post)} role="button" aria-label="Popup" tabIndex={"0"}>
              <AuthorDetails Post={Post} CardStyle={CardStyle} ThemeStyle={ThemeStyle} mediaType={mediaType} size={40} rating={Post.rating} inView={inView} />
              {(isTagShop && CardStyle.content.status) ? <div className="tb_l_contant_wrapper">
                  <PostContent contentClass={`tb_l_content`} Post={Post} content={postTextContent} CardStyle={CardStyle} contentTitle={Post.contentTitle} /> 
              </div> : ''}
              {mediaType ? <div className="tb_l_media_cont"><Media Post={Post} CardStyle={CardStyle} ThemeStyle={ThemeStyle} inView={inView} /></div> : ''}
              {Post.isProduct ? <div className="tb_l_p_list">
                <div className="tb_l_p_wrp">
                  <div className="tb_l_p_">
                      <img className={'tb_l_p_img'} decoding="async" src={products[0]?.image} width="60" height="60" alt={products[0]?.title} loading="lazy" draggable="false" onError={(e) => e.target.src = NO_PRODUCT} />
                  </div>
                  <div className="tb_l_p_info">
                    <div className="tb_l_p_t">{products[0]?.title}</div>
                    <div className="tb_l_p_p">{products[0]?.currency}{products[0]?.discount > 0 ? products[0]?.discount : products[0]?.price}</div>
                  </div>
                </div>
              </div> : null}
            </div>
          </div>)}
        </InViewWrapper>
}
export default Card