import React from "react";
import Image from "../../Elements/Media/Image";

const MediaIconCenter =()=> {
  return <div className={`tb_l_media_icon tb__media_ico_c`}>
            <div className="tb_center_video_ico tb__icon tb-play-circle"><div></div></div>
        </div>
}
const Media = ({ Post, inView }) => {
  return Post.mediaList.length > 0 ? 
        Post.mediaList.map((Item, index) => {
          return <div  className="tb_l_media_wrap" role="button" tabIndex="0" aria-label="Pop Up Button">
                  {[3,5].includes(Item.type)? <MediaIconCenter /> : null}
                  <Image key={index} ImageClass="tb_l_image" Post={Post} size={1} mediaOnly={Item} hotspot={false} blurBG={false} observer={true} inView={inView} />  
                </div>
          })
        :
        <div className="tb_l_media_wrap" role="button" tabIndex="0" aria-label="Pop Up Button">
        {[3,5].includes(Post.type)? <MediaIconCenter /> : null}
        <Image ImageClass="tb_l_image" Post={Post} size={1} hotspot={false} blurBG={false} observer={true} inView={inView} /> 
        </div>
}
export default Media