import Author from "../../Elements/Author/Author";
import Time from "../../Elements/Time/Time";
import AverageRating from "../../Elements/AverageRating/AverageRating";
import { isInstaUser } from "../../../../utils";
const AuthorDetails = ({ Post, CardStyle, inView }) => {
  const { author, networkId } = Post
  const instaUser =  isInstaUser(author.name);
  const postAuthor = (CardStyle.author.status  && !instaUser) ? true : false;
  const isRating = (Post.rating > 0) ? true : false;
  return <div className="tb_l_author_wrapper" aria-label="Author details" aria-roledescription="post author details">
    <div className="tb_l_author">
      {postAuthor ? <Author author={author} network={Post.network} networkId={networkId} authorClass={'tb_l_author_profile'} inView={inView} /> : ''}
      <div className="tb_l_author_info">
        {postAuthor ? <div className="tb_l_authorname">{author.name}</div> : ''}
        {CardStyle.timeStatus ? <Time createdAt={Post.createdAt}  timeClass={`tb_l_time tb-cTBfont-${CardStyle.author.variant}`} /> : ''}
      </div>
    </div>
    {isRating ? <div className="tb_l_rating__"><AverageRating size={16} rating={Post.rating} /> </div> : ''}
  </div>
}
export default AuthorDetails